.swiper {
    width: 422px;
    height: 422px;
    margin: 0 auto 150px;
}

.swiper-slide+.swiper-slide {
    margin-left: 40px;
}

.swiper-slide{
    opacity: 0.3;
}

.swiper-slide-active{
    opacity: 1;
}

.custom-slide {
    width: 422px;
    width: 100%;
    height: 422px;
    background-color: #c86f8d;
    position: relative;

    &__content{
        width: 100px;
        height: 100px;
        text-align: center;
        position: absolute;
        left: 0;
        right: 0;
        top: 50%;   
        transform: translateY(-50%);
        margin: 0 auto;
        img{
            width: 80px;
            height: 80px;
            object-fit: cover;
        }
    }

    &__info {
        margin-top: 50px;
        color: #ffffff;
        font-size: 22px;
        line-height: 100%;
        font-weight: 500;
        font-family: 'Montserrat-Medium';
    }
}

@media(max-width: 768px){
    .swiper {
        width: 350px;
        height: 350px;
    }
    .custom-slide{
        height: 350px;
    }
}

@media(max-width: 576px){
    .swiper {
        margin: 0 auto 90px;
        width: 290px;
        height: 290px;
    }
    .custom-slide{
        height: 290px;
    }
}

@media(max-width: 420px){
    .swiper {
        width: 230px;
        height: 230px;
    }
    .custom-slide{
        height: 230px;
    }
}