.community {
    .community-user {
        border-right: 2px solid #b3b3b3;
        padding-right: 20px;
    }

    &__item {
        max-width: 522px;

        .user__list {
            position: relative;

            .user-link {
                position: absolute;
                bottom: -46px;
            }
        }

        .user__item+.user__item {
            margin-top: 40px;
        }
    }

    &__inner {
        padding-bottom: 70px;
        display: grid;
        grid-gap: 16px;
        grid-template-columns: repeat(auto-fit, 520px);
    }

    &__about {
        display: flex;
        align-items: center;
        position: relative;
        margin-bottom: 56px;
    }

    &__about-secondary {
        margin-left: 40px;
        max-width: 300px;
        width: 100%;
    }

    .marker-btn {
        z-index: 100;
        position: absolute;
        right: 0;
        top: 0;
    }

    &__about-title {
        padding-left: 10px;
        font-size: 18px;
        line-height: 100%;
        font-weight: 700;
        font-family: 'Montserrat-Bold';
        color: #161616;
        margin-bottom: 6px;
    }

    &__content {
        margin-bottom: 82px;
    }

    &__about-text {
        resize: none;
        box-sizing: border-box;
        border: 0;
        width: 300px;
        height: 100px;
        padding: 10px;
        width: 100%;
        color: #3c3c3c;
        font-size: 16px;
        line-height: 100%;
        font-weight: 400;
        font-family: 'Montserrat-Regular';
        &::placeholder{
            font-weight: 400;
            font-family: 'Montserrat-Regular';
            color: #3c3c3c;
            font-size: 16px;
            line-height: 100%;
        }
    }

    &__content-title {
        color: #161616;
        line-height: 100%;
        font-weight: 700;
        font-family: 'Montserrat-Bold';
        margin-bottom: 20px;
    }

    &__content-link{
        max-width: 68px;
        width :100%;
        img{
            width: 68px;
            object-fit: cover;
            height: 68px;
        }
    }

    &__content-list {
        display: grid;
        grid-column-gap: 17px;
        grid-row-gap: 38px;
        grid-template-columns: repeat(auto-fit, 66px);
    }

}

@media(max-width: 1670px) {
    .community{
        &__inner{
            grid-row-gap: 60px;
            grid-template-columns: repeat(auto-fit, 535px);
        }
        &__item{
            margin: 0;
        }
        .user-link{
            top: 0;
            right: 0;
        }
    }
}

@media(max-width: 1100px){
    .community{
        &__inner{
            justify-content: center;
        }
        .community-user{
            padding-right: 0;
            border-right: none;
        }
    }
}

@media(max-width: 576px) {
    .community{
        &__inner{
            grid-template-columns: repeat(auto-fit, 390px);
        }
    }
}

@media(max-width: 420px) {
    .community{
        &__inner{
            grid-template-columns: repeat(auto-fit, 290px);
        }
    }
}