.sidebar-second {
    max-width: 300px;
}

.news {

    &__title {
        margin-bottom: 18px;
        font-size: 26px;
        line-height: 100%;
        font-weight: 500;
        text-transform: uppercase;
        font-family: 'Montserrat-Medium';
    }

    &__data {
        font-size: 12px;
        line-height: 100%;
        font-weight: 500;
        font-family: 'Montserrat-Medium';
        margin-bottom: 14px;
        color: #d7a1b2;
    }

    &__img {
        max-width: 70px;
        margin-bottom: 14px;
        img {
            width: 100%;
            object-fit: cover;
        }
    }

    &__item+&__item {
        margin-top: 20px;
    }

    &__link{
        display: block;
        max-width: max-content;
        width: 100%;
        margin-left: auto;
        font-size: 12px;
        line-height: 100%;
        font-weight: 500;
        font-family: 'Montserrat-Medium';
        box-sizing: border-box;
        padding-bottom: 2px;
        color: #d7a1b2;
        border-bottom: 1px solid #d7a1b2;
    }

    &__info{
        font-size: 16px;
        line-height: 100%;
        font-weight: 400;
        font-family: 'Montserrat-Regular';
        margin-bottom: 14px;
    }

    &__btn{
        margin-top: 50px;
    }

}