 .footer {
     padding: 50px 0;
     background-color: #ffffff;
     border-top: 1px solid #bebebe;

     &__inner {
         display: flex;
     }

     .user__list {
         margin-left: 50px;
         display: grid;
         grid-template-columns: repeat(2, 110px);
         grid-column-gap: 100px;
         grid-template-columns: repeat(2, 110px);
         grid-row-gap: 28px;

         &__item+&__item {
             margin-top: 0;
         }
     }

     .user {
         &__link {
             font-size: 16px;
             color: #aeaeae;
             transition: all .3s;

             &:hover {
                 color: #0056b3;
             }
         }
     }

     .map {
         margin-left: auto;
         max-height: 410px;
         max-width: 425px;
         width: 100%;

         iframe {
             width: 100%;
             height: 100%;
         }
     }

 }

 @media (max-width: 992px) {
     .footer {
         &__inner {
             flex-wrap: wrap;
         }

         .map {
             margin-top: 26px;
             max-width: 100%;
             flex-grow: 4;
             max-height: 500px;

             iframe {
                 height: 450px;
             }
         }

     }
 }

 @media (max-width: 768px) {
     .footer {
         padding-bottom: 30px;

         .logo {
             text-align: center;
             margin-bottom: 25px;
         }

         &__inner {
             flex-direction: column;
         }

         .user__list {
             justify-content: center;
             margin-left: 0;
             grid-column-gap: 50px;
         }
     }

 }

 @media (max-width: 576px) {
     .logo {
         font-size: 24px;
     }

     .footer {

         .logo {
             padding-left: 10px;
         }
     }
 }

 @media (max-width: 420px) {}