.sidebar-main {
    padding: 0 24px;
    margin: 0 auto;
    max-width: 1030px;
}

.master {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;

    &__title {
        font-size: 26px;
        line-height: 100%;
        font-weight: 500;
        font-family: 'Montserrat-Medium';
        margin-bottom: 30px;
    }

    &__list {
        max-width: 100%;
        display: flex;
    }

    &__item+&__item {
        margin-left: 40px;
    }

    &__link-img {
        margin-bottom: 10px;
        max-width: 214px;
        width: 100%;

        img {
            max-width: 214px;
            height: 214px;
            object-fit: cover;
        }
    }

    &__link-title {
        font-weight: 500;
        font-family: 'Montserrat-Medium';
        font-size: 22px;
        line-height: 100%;
    }

    &__link-subtitle {
        margin-top: 6px;
        font-size: 16px;
        line-height: 100%;
        font-weight: 400;
        font-family: 'Montserrat-Regular';
        display: block;
    }
}

@media(max-width: 1920px) {}

@media(max-width: 1500px) {
    .sidebar-main {
        max-width: 750px;
    }

    .master {
        &__list {
            overflow-x: scroll;
        }
    }
}

@media(max-width: 1200px) {
    .sidebars {
        position: relative;
        min-height: 600px;
    }

    .master {
        margin: 0;
    }

    .sidebar-second.news {
        border-radius: 10px;
        padding: 10px 14px;
        background-color: #B1A7C9;
        z-index: 100;
        position: absolute;
        right: 0;
        top: 0;
        max-height: 600px;
        overflow-y: scroll;
    }
}

@media (max-width: 992px) {}

@media (max-width: 768px) {
    .sidebars {
        flex-direction: column;
    }

    .sidebar-second.news {
        top: -63px;
        max-height: 514px;
    }

    .master {
        padding-left: 0;
        margin-top: 48px;
    }
}

@media (max-width: 576px) {
    .sidebars {
        padding-top: 486px;
    }

    .sidebar-second.news {
        padding: 24px 14px;
        max-width: 100%;
    }
}

@media (max-width: 420px) {}