.registr {
    margin-bottom: 134px;

    &__title {
        text-align: center;
        font-size: 22px;
        line-height: 100%;
        text-transform: uppercase;
        font-weight: 500;
        font-family: 'Montserrat-Medium';
        margin-bottom: 78px;
    }

    &__wrapper {
        display: flex;
        flex-direction: column;
        justify-content: center;
        max-width: 520px;
        width: 100%;
        margin: 0 auto;
    }

    &__select {
        cursor: pointer;
        font-size: 16px;
        line-height: 100%;
        color: #949494;
        border: none;
        border-bottom: 1px solid #d6d6d6;
        box-sizing: border-box;
        padding: 7px 20px;
        min-width: 520px;
        -webkit-appearance: none;
        -moz-appearance: none;
        -ms-appearance: none;
        appearance: none;
        margin: 0;
    }

    &-inner {
        position: relative;
        margin-top: 30px;
        text-align: left;

        &::after {
            position: absolute;
            content: "";
            width: 16px;
            height: 10px;
            right: 10px;
            top: 50%;
            transform: translateY(-50%);
            background-position: center;
            background-size: cover;
            background-repeat: no-repeat;
            background-image: url('/assets/images/icons/select-icon.png');
        }
    }

    .checkbox-wrapper {
        margin: 56px 0 40px;
        display: flex;
        align-items: center;
        cursor: pointer;
        transition: all .3s;

        &:hover {
            .checkbox-wrapper__text {
                color: #007bff;
            }
        }

        &__input {
            -webkit-appearance: none;
            position: absolute;
            z-index: -1;
        }

        .my-checkbox {
            margin-right: 12px;
            position: relative;
            width: 26px;
            height: 26px;
            border: 2px solid #dbdbdb;
            background-color: transparent;
        }

        .checkmark {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            width: 12px;
            height: 12px;
            background-color: #c76f8c;
            display: none;
        }

        & input:checked~.my-checkbox .checkmark {
            display: block;
            background-color: #c76f8c;
        }

        &__text {
            font-weight: 400;
            font-family: 'Montserrat-Regular';
            color: #3c3c3c;
            font-size: 16px;
            line-height: 100%;
        }
    }

    &__option {
        font-size: 16px;
        line-height: 100%;
        color: #949494;
    }

    &__input {
        font-size: 16px;
        line-height: 100%;
        color: #949494;
        border: none;
        border-bottom: 1px solid #d6d6d6;
        box-sizing: border-box;
        padding: 7px 20px;
        min-width: 520px;
        transition: all .3s;
        cursor: pointer;

        &::placeholder {
            font-size: 16px;
            line-height: 100%;
            color: #949494;
        }

        &:focus {
            color: #ffffff;

            &::placeholder {
                color: #ffffff;
            }

            background-color: #949494;
        }
    }

    &__input+&__input {
        margin-top: 34px;
    }

    &__btn.primary-btn {
        margin: 60px auto 0;
        display: block;
    }
}

@media(max-width: 768px){
    .registr{
        margin-bottom: 86px;
    }
}

@media(max-width: 576px) {
    .registr {
        &__wrapper {
            max-width: 380px;
        }

        &__input, &__select {
            min-width: 380px
        }
    }
}

@media(max-width: 420px){
    .registr {
        &__wrapper {
            max-width: 290px;
        }

        &__input, &__select {
            min-width: 290px
        }
    }
}