body {
    font-family: 'Montserrat-Regular';
    font-weight: 400;
}

html,
body {
    overflow-x: hidden;
    margin: 0;
    padding: 0;
    height: 100%;
}

.primary-btn {
    text-align: center;
    min-width: 170px;
    background-color: #c86f8d;
    padding: 5px 12px;
    color: #ffffff;
    font-size: 18px;
    font-weight: 400;
    font-family: 'Montserrat-Regular';
}

.secondary-link {
    background-color: #000000;
    color: #ffffff;
    font-size: 16px;
    line-height: 100%;
    font-weight: 400;
    font-family: 'Montserrat-Regular';
    padding: 10px 26px;
}

.btn-text {
    background-color: #c86f8d;
    padding: 10px 18px 10px 55px;
    color: #ffffff;
    font-size: 16px;
    line-height: 100%;
    font-weight: 400;
    font-family: 'Montserrat-Regular';
    position: relative;
    transition: all .3s;
    &:hover{
        color: #0056b3;
    }
    &::before {
        position: absolute;
        content: "";
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        width: 14px;
        height: 12px;
        left: 18px;
        top: 50%;
        transform: translateY(-50%);
        background-image: url('/assets/images/icons/send-btn-icon.png');
    }
}

.btn-videocall {
    background-color: #c86f8d;
    color: #ffffff;
    padding: 10px 18px 10px 55px;
    font-size: 16px;
    line-height: 100%;
    font-weight: 400;
    font-family: 'Montserrat-Regular';
    position: relative; 
    margin-left: 40px;
    transition: all .3s;
    &:hover{
        color: #0056b3;
    }
    &::before {
        position: absolute;
        content: "";
        width: 16px;
        height: 10px;
        left: 18px;
        top: 50%;
        transform: translateY(-50%);
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        background-image: url('/assets/images/icons/video-btn-icon.png');
    }
}

.logo {
    font-size: 28px;
    line-height: 100%;
    color: #000000;
    font-weight: 500;
    font-family: 'AktivGroteskCorp-Medium';

    &-custom {
        color: #ffffff;
    }

    img {
        height: 34px;
        object-fit: cover;
    }
}

.horizontal {
    margin: 0;
}

.container {
    max-width: 1640px;
    margin: 0 auto;
    padding: 0 10px;
}