.service {
    &__title {
        text-transform: uppercase;
        font-size: 22px;
        line-height: 100%;
        font-weight: 500;
        font-family: 'Montserrat-Medium';
        color: #000000;
        margin-bottom: 36px;
    }

    &__list {
        display: grid;
        grid-column-gap: 53px;
        grid-row-gap: 24px;
        grid-template-columns: repeat(auto-fit, 120px);
    }

    &__item-title {
        margin-top: 14px;
        text-align: center;
    }

}

@media(max-width: 576px) {
    .service {
        &__list {
            justify-content: center;
        }
    }
}
