.sidebars {
    display: flex;
}

.sidebar-first {
    max-width: 300px;

    .user {
        &-link {
            position: absolute;
            bottom: -50px;
            width: 20px;
            height: 20px;

            img {
                width: 20px;
                height: 20px;
                object-fit: cover;
            }
        }

        &__list {
            position: relative;
        }
    }

    .user__item+.user__item {
        margin-top: 32px !important;
    }
}

.user {

    &__greetings {
        margin-bottom: 20px;
        font-size: 18px;
        line-height: 100%;
        font-weight: 400;
        color: #7f7e7e;
    }

    &__img {
        margin-bottom: 30px;
        max-width: 70px;
        margin-bottom: 54px;

        img {
            width: 70px;
            height: 70px;
            object-fit: cover;
        }
    }

    &__link {
        color: #000000;
        font-size: 22px;
        line-height: 100%;
        font-weight: 500;
        font-family: 'Montserrat-Medium';
    }

}