.header {
    margin-bottom: 60px;
    padding: 24px 0;

    &__inner {
        display: flex;
        align-items: center;
    }

    .service {
        margin-left: auto;
        margin-right: 40px;
    }

    .search {
        margin-left: 14px;
        max-width: 190px;
        position: relative;

        &::before {
            position: absolute;
            content: "";
            left: 4px;
            top: 50%;
            transform: translateY(-50%);
            width: 20px;
            height: 20px;
            background-image: url('/assets/images/icons/search-icon.png');
            background-repeat: no-repeat;
            background-size: cover;
        }

        &__input {
            max-width: 190px;
            border: none;
            background-color: transparent;
            box-sizing: border-box;
            padding-bottom: 6px;
            padding-left: 28px;
            border-bottom: 1px solid #605F5A;
        }
    }
}

@media (max-width: 768px) {
    .header {
        .service.primary-btn {
            min-width: 106px;
        }

        .search {
            &__input {
                max-width: 106px;
            }
        }
    }
}

@media (max-width: 576px) {
    .header {
        &__inner {
            flex-direction: column;

            .service.primary-btn {
                margin: 14px auto;
            }

            .search {
                margin: 0 auto;
            }
        }

        .search__input {
            max-width: 190px;
        }
    }
}