a {
    text-decoration: none !important;
    transition: all .3s;
    display: inline-block;
    color: inherit;
}

h1,
h2,
h3,
h4, p {
    margin: 0;
}

ul {
    padding: 0;
    list-style: none;
    margin: 0;
}

.text {
    p {
        margin-bottom: 0;
    }
}

button {
    padding: 0;
    cursor: pointer;
    box-sizing: border-box;
    background-color: transparent;
    border: none;
}

