.master {
    padding-left: 20px;

    &__title {
        font-size: 22px;
        line-height: 100%;
        color: #000000;
        text-transform: uppercase;
        font-weight: 500;
        font-family: 'Montserrat-Medium';
        margin-bottom: 46px;
    }

    &__primary {
        margin-bottom: 50px;
    }

    &__primary-img {
        max-width: 165px;
        width: 100%;

        img {
            width: 165px;
            height: 165px;
            object-fit: cover;
        }
    }

    &__primary-inner {
        display: flex;
    }

    &__primary-info {
        margin-left: 30px;
    }

    &__primary-title {
        font-size: 22px;
        line-height: 100%;
        color: #000000;
        font-weight: 500;
        font-family: 'Montserrat-Medium';
        margin-bottom: 35px;
    }

    &__primary-subtitle {
        margin-top: 10px;
        display: block;
        font-size: 16px;
        line-height: 100%;
        font-weight: 400;
        font-family: 'Montserrat-Regular';
    }

    &__primary-text {
        color: #3c3c3c;
        font-size: 16px;
        line-height: 100%;
        font-weight: 400;
        font-family: 'Montserrat-Regular';
        max-width: 218px;
    }

    &__secondary-title {
        color: #161616;
        font-size: 18px;
        line-height: 100%;
        font-weight: 700;
        font-family: 'Montserrat-Bold';
        margin-bottom: 10px;
    }

    &__social-btn {
        margin-bottom: 28px;
    }

    &__secondary-text {
        margin-bottom: 45px;
    }

}

@media(max-width :970px) {
    .master {
        &__social-btns {
            display: flex;
            flex-direction: column;

            .btn-videocall {
                margin-top: 20px;
                margin-left: 0;
            }
        }
    }
}

@media(max-width :768px) {
    .community-master {
        .master {
            margin-top: 0;
            padding-left: 0;
        }
    }
}