.chat {
    &__title {
        text-transform: uppercase;
        font-weight: 500;
        font-family: 'Montserrat-Medium';
        font-size: 22px;
        line-height: 100%;
        color: #000000;
        margin-bottom: 36px;
    }

    &__inner {
        position: relative;
        display: none;
    }

    &__el {
        position: relative;
        z-index: 100;
        max-height: 670px;
        overflow-y: scroll;
        width: 520px;
        padding: 40px;
        background-color: #ffffff;

        &-inner {
            background-color: #eeeeee;
            padding: 45px 40px 30px 40px;
        }

        &-btn {
            text-align: left;
            width: 100%;
            padding-bottom: 38px;
            border-bottom: 1px solid #ffffff;
            margin-bottom: 40px;
            transition: all .3s;

            &:hover {
                background-color: #F4DCDC;
            }
        }

        .professional {
            margin-bottom: 40px;
            display: flex;

            &__img {
                max-width: 79px;
                width: 100%;

                img {
                    width: 79px;
                    height: 79px;
                    object-fit: cover;
                }
            }

            &__chat {
                background-color: #ffffff;
                padding: 24px;
            }

            &__message {
                font-size: 16px;
                line-height: 100%;
                color: #696969;
                font-weight: 400;
                font-family: 'Montserrat-Regular';
            }

            .time {
                margin-top: 16px;
                font-size: 10px;
                line-height: 100%;
                display: block;
                font-weight: 400;
                font-family: 'Montserrat-Regular';
            }
        }

        .user {
            display: flex;
            margin-bottom: 40px;

            &__chat {
                background-color: #ffffff;
                padding: 24px;
            }

            &__message {
                font-size: 16px;
                line-height: 100%;
                color: #696969;
                font-weight: 400;
                font-family: 'Montserrat-Regular';
            }

            &__img {
                max-width: 121px;
                width: 100%;

                img {
                    width: 121px;
                    height: 121px;
                    object-fit: cover;
                }
            }

            .time {
                margin-bottom: 16px;
                font-size: 10px;
                line-height: 100%;
                display: block;
                font-weight: 400;
                font-family: 'Montserrat-Regular';
            }
        }

        &-title {
            font-weight: 500;
            font-family: 'Montserrat-Medium';
            font-size: 26px;
            line-height: 100%;
            text-transform: uppercase;
            margin-bottom: 26px;
        }
    }

    &__el-form {
        margin-top: 140px;
        max-width: 470px;
        display: flex;
    }

    &__el-textarea {
        padding: 14px;
        resize: none;
        box-sizing: border-box;
        border: none;
        width: 420px;
        height: 110px;
        font-weight: 400;
        font-family: 'Montserrat-Regular';
        font-size: 18px;
        line-height: 100%;
    }

    &__el-send {
        width: 52px;
        height: 62px;
        background-color: #c86f8d;
    }

    &__list {
        background-color: #eeeeee;
        box-sizing: border-box;
        padding: 45px;
        overflow-y: scroll;
        max-height: 670px;

        &::-webkit-scrollbar {
            width: 11px;
        }

        &::-webkit-scrollbar-track {
            background-color: #e8e8e8;
        }

        &::-webkit-scrollbar-thumb {
            background-color: #d8d8d8;
        }
    }

    &__item+&__item {
        margin-top: 40px;
    }

    &__link {
        display: flex;
        border-bottom: 1px solid #ffffff;
        padding-bottom: 40px;
        position: relative;
        transition: all .3s;

        &:hover {
            .chat__link-title {
                color: #0056b3;
            }
        }

        &::after {
            position: absolute;
            content: "";
            right: 0;
            top: 21%;
            transform: translateY(-50%);
            width: 12px;
            height: 9px;
            background-repeat: no-repeat;
            background-size: cover;
            background-position: center;
            background-image: url('/assets/images/icons/chat-item-arrow.png');
        }
    }

    &__link-title {
        font-size: 22px;
        list-style: 100%;
        font-weight: 500;
        font-family: 'Montserrat-Medium';
        color: #010101;
    }

    &__link-subtitle {
        display: block;
        margin-top: 10px;
        font-size: 16px;
        line-height: 100%;
        font-weight: 400;
        font-family: 'Montserrat-Regular';
    }

    &__link-img {
        max-width: 79px;
        width: 100%;
        pointer-events: none;

        img {
            width: 79px;
            object-fit: cover;
            height: 79px;
        }
    }

    &__link-info {
        pointer-events: none;
        margin-left: 28px;
    }
}

.chat__inner-active {
    display: block;
}

.chat__inner-hidden {
    display: none;
}

.chat__list-hidden {
    display: none;
}

.chat__list-active {
    display: block;
}

@media(max-width: 576px) {
    .chat {
        &__el {
            width: 390px;
            padding: 0;
        }

        &__list {
            padding: 20px;
        }

        &__el-inner {
            padding: 45px 20px 30px;
        }
    }

    .professional {
        &__img {
            img {
                width: 79px;
                height: 79px;
            }
        }
    }
}

@media(max-width: 420px) {
    .chat {
        &__el {
            width: 290px;

            .user__chat,
            .professional__chat {
                padding: 12px;
            }

            .user__message,
            .professional__message {
                font-size: 14px;
            }
        }

        &__el-textarea {
            width: 330px;
        }
    }
}